import React, { useContext, useEffect } from "react";
import { Store } from "store";
import { fetchAllSubjects } from "actions/subjects";
import { fetchAllCategories } from "actions/subjects";
import { fetchAllTags } from "actions/question";
import { fetchAllQuestions } from "actions/question";
import { getCookie } from "utils/cookie";

const Init = () => {
  const { state, dispatch } = useContext(Store);
  useEffect(() => {
    fetchAllSubjects(dispatch);
    fetchAllCategories(dispatch);
    fetchAllTags(dispatch);
    if (getCookie("token")) {
      fetchAllQuestions(dispatch);
    }
  }, []);
  return null;
};
export default Init;
