import "../../assets/scss/mimir/questionfeeds.scss";
import React, { useEffect, useContext } from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Parallax from "../../components/Parallax/Parallax.js";

import styles from "assets/jss/material-kit-react/views/landingPage.js";
import mainStyles from "assets/jss/material-kit-react/views/components.js";

// Sections for this page
import { Typography, Paper, IconButton, InputBase } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import { Store } from "store/index.js";
import { fetchAllSubjects } from "actions/subjects.js";
import { useHistory } from "react-router-dom";
import { fetchAllQuestionsBySubject } from "actions/question";
import { fetchQuestionDetails } from "actions/question";

const useStyles = makeStyles(styles);
const useMainStyles = makeStyles(mainStyles);

export default function QuestionFeeds(props) {
  const history = useHistory();
  const { state, dispatch } = useContext(Store);
  const { allQuestions } = state.question;
  const { subjects } = state.subjects;

  const filterBySubject = subjectId => {
    history.push(`?subjectId=${subjectId}`);
    fetchAllQuestionsBySubject(dispatch);
  };

  const onQuestionClick = question => {
    fetchQuestionDetails(dispatch, question);
    history.push(`/question/${question.questionId}`);
  };

  useEffect(() => {
    // fetchAllSubjects(dispatch);
  }, [dispatch]);
  return (
    <div className="question-feeds-container">
      <GridContainer>
        <GridItem xs={12} sm={2} md={2}>
          <div className="question-filter-container">
            <Typography variant="subtitle2">Filter by subjects</Typography>
            <div className="filter-subject-contaner">
              {Array.isArray(subjects) &&
                subjects.map(sub => (
                  <div
                    className="filter-subject-item"
                    onClick={() => filterBySubject(sub.id)}
                  >
                    {sub.name}
                  </div>
                ))}
            </div>
          </div>
        </GridItem>
        <GridItem xs={12} sm={7} md={7} className="question-list-wrapper">
          <Typography variant="h5" component="h5">
            Top Questions
          </Typography>
          <div className="question-list-container">
            {Array.isArray(allQuestions) &&
              allQuestions.map(qn => (
                <div className="question-item-container">
                  <div className="question-item-answered">
                    <div className="answer-count">4</div>
                    <div className="answer-label">Answers</div>
                  </div>
                  <div className="question-item-title">
                    <div
                      className="question-title"
                      onClick={() => onQuestionClick(qn)}
                    >
                      {qn.title}
                    </div>
                    <div className="question-subject">
                      {qn.subjectName && <span>{qn.subjectName}</span>}
                      {qn.categoryName && <span>{qn.categoryName}</span>}
                    </div>
                    <div className="question-tags">
                      {qn.tag1 && <span>{qn.tag1}</span>}
                      {qn.tag2 && <span>{qn.tag2}</span>}
                      {qn.tag3 && <span>{qn.tag3}</span>}
                      {qn.tag4 && <span>{qn.tag4}</span>}
                      {qn.tag5 && <span>{qn.tag5}</span>}
                    </div>
                  </div>
                  <div className="question-item-user">
                    {qn.userPicture ? (
                      <img
                        src={qn.userPicture}
                        className="question-user-image"
                      />
                    ) : (
                      <div className="question-user-letter">
                        {qn.userName[0]}
                      </div>
                    )}
                    <div className="question-user-details">
                      <div className="question-user-name">{qn.userName}</div>
                      <div className="question-user-answered">Answered: 10</div>
                    </div>
                  </div>
                </div>
              ))}
          </div>
        </GridItem>
        <GridItem xs={12} sm={3} md={3}>
        </GridItem>
      </GridContainer>
      <Footer />
    </div>
  );
}
