import {
  SET_QUESTION_DATA,
  SET_ALL_TAGS,
  SET_ALL_QUESTIONS
} from "constants/action_names";
import { getAllTags } from "services/question";
import { getAllQuestions } from "services/question";
import { getQueryParams } from "utils/common";
import {
  getAllQuestionsBySubjectId,
  getQuestionDetails
} from "services/question";
import { SET_QUESTION_DETAILS } from "constants/action_names";

export const setQuestionData = (payload, dispatch) => {
  dispatch({
    type: SET_QUESTION_DATA,
    payload
  });
};

export const setAllTags = (payload, dispatch) => {
  dispatch({
    type: SET_ALL_TAGS,
    payload
  });
};

export const setAllQuestions = (payload, dispatch) => {
  dispatch({
    type: SET_ALL_QUESTIONS,
    payload
  });
};

export const setQuestionDetails = (payload, dispatch) => {
  dispatch({
    type: SET_QUESTION_DETAILS,
    payload
  });
};

export const changeQuestionData = (questionData, key, value, dispatch) => {
  questionData[key] = value;
  setQuestionData(questionData, dispatch);
};

export const fetchAllTags = async dispatch => {
  try {
    const tags = await getAllTags();
    if (Array.isArray(tags)) {
      setAllTags(tags, dispatch);
    }
  } catch (e) {
    console.error("error to fetching all subjects", e);
  }
};

export const fetchAllQuestions = async dispatch => {
  setAllQuestions(null, dispatch);
  const pageNo = getQueryParams("pageNo") ? getQueryParams("pageNo") : 1;
  const pageSize = getQueryParams("pageSize") ? getQueryParams("pageSize") : 10;
  const subjectId = getQueryParams("subjectId");
  if (subjectId) {
    fetchAllQuestionsBySubject(dispatch);
  } else {
    try {
      const questions = await getAllQuestions(parseInt(pageNo) - 1, pageSize);
      if (Array.isArray(questions)) {
        setAllQuestions(questions, dispatch);
      } else {
        setAllQuestions([], dispatch);
      }
    } catch (e) {
      console.error("error to fetching all subjects", e);
    }
  }
};

export const fetchAllQuestionsBySubject = async dispatch => {
  setAllQuestions(null, dispatch);
  const pageNo = getQueryParams("pageNo") ? getQueryParams("pageNo") : 1;
  const pageSize = getQueryParams("pageSize") ? getQueryParams("pageSize") : 10;
  const subjectId = getQueryParams("subjectId");
  if (!subjectId) {
    setAllQuestions([], dispatch);
    return;
  }
  try {
    const questions = await getAllQuestionsBySubjectId(
      parseInt(pageNo) - 1,
      pageSize,
      subjectId
    );
    if (Array.isArray(questions)) {
      setAllQuestions(questions, dispatch);
    } else {
      setAllQuestions([], dispatch);
    }
  } catch (e) {
    setAllQuestions([], dispatch);
    console.error("error to fetching all subjects", e);
  }
};

export const fetchQuestionDetails = async (dispatch, question) => {
  setQuestionDetails(question, dispatch);
  try {
    const qnd = await getQuestionDetails(question.questionId);
    if (qnd) {
      setQuestionDetails({ ...question, ...qnd }, dispatch);
    }
  } catch (e) {
    console.error("error to fetching all subjects", e);
  }
};
